* {
    padding: 0;
    margin: 0;
    
    font-style: italic;

}

.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 1rem;
    position: fixed;
    width: 100%;
    height: 90px;
    z-index: 10;
}

.header-bg{
    background-color: rgba(0,0,0,0.85);
    transition: .5s;
}









.header img {
    height: 5rem;
    margin-left: 2rem;
}

.nav-menu {
    display: flex;

}

.nav-menu li {
    padding: 0 1rem;
}

.nav-menu li a {
    font-size: 1.2rem;
    font-weight: 500;
}

.nav-menu li a:hover {
    color: red;
    transition: .4s;
    background: rgba(255, 255, 255, 0.2);
    color: #fff;
    transition: 0.3s;
    padding: 12px 13px;
    font-size: 1rem;
    text-transform: uppercase;
    background: rgb(248, 217, 15);
    color: #222;
    border: 1px solid #fff;
    font-weight: 500;
}








.hamburger {
    display: none;
}


@media screen and (max-width: 840px) {
    .nav-menu {
        flex-direction: column;
        justify-content: center;
        align-items: center;
        width: 100%;
        height: 100vh;
        background: rgba(0, 0, 0, 0.9);
        position: absolute;
        top: 0;
        left: -100%;
        z-index: -3;
        transition: 0.3s;
    }

    .nav-menu.active {
        left: 0;
    }




    .nav-menu li {
        padding: 1rem 0;
    }

    .nav-menu li a {
        font-size: 1.6rem;
    }

    .hamburger {
        display: initial;
    }








}